.page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #f8fafc;
  padding: 32px;
}

.panel {
  width: 100%;
  max-width: 560px;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 28px;
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(6px);
}

.title {
  margin: 0 0 8px;
  font-size: 28px;
  letter-spacing: -0.02em;
}

.subtitle {
  margin: 0 0 20px;
  color: #cbd5e1;
}

.dropzone {
  position: relative;
  border: 1px dashed #94a3b8;
  border-radius: 12px;
  padding: 32px;
  text-align: center;
  transition: all 150ms ease;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.02);
}

.dropzone:hover {
  border-color: #e2e8f0;
  color: #e2e8f0;
}

.dropzone--active {
  border-color: #38bdf8;
  background: rgba(56, 189, 248, 0.08);
  color: #e0f2fe;
  box-shadow: 0 10px 40px rgba(56, 189, 248, 0.3);
}

.dropzone__text {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.01em;
}

.file-input {
  position: absolute;
  inset: 0;
  opacity: 0;
  cursor: pointer;
}

.notice {
  margin-top: 14px;
  padding: 12px 14px;
  border-radius: 10px;
  font-size: 14px;
}

.notice--error {
  background: rgba(248, 113, 113, 0.1);
  border: 1px solid rgba(248, 113, 113, 0.45);
  color: #fecdd3;
}

.stack {
  margin-top: 18px;
  padding: 18px;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.stack__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 12px;
}

.stack__title {
  margin: 0;
  font-weight: 700;
  letter-spacing: 0.01em;
  color: #e2e8f0;
}

.stack__time {
  font-variant-numeric: tabular-nums;
  color: #cbd5e1;
  font-size: 14px;
}

.controls {
  display: flex;
  gap: 10px;
  align-items: center;
}

.control-row {
  margin: 8px 0 4px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
}

.control-row--full {
  justify-content: stretch;
}

.mic-select {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: #cbd5e1;
  font-size: 14px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 10px;
  padding: 6px 10px;
  margin: 6px 0;
}

.mic-select--full {
  width: 100%;
}

.mic-select__label {
  font-weight: 600;
}

.select {
  background: transparent;
  color: #e2e8f0;
  border: none;
  font-size: 14px;
  outline: none;
  width: 100%;
}

.button {
  appearance: none;
  border: none;
  border-radius: 10px;
  padding: 10px 14px;
  background: linear-gradient(135deg, #38bdf8, #0ea5e9);
  color: #0b1224;
  font-weight: 700;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease, opacity 120ms ease;
  box-shadow: 0 8px 20px rgba(14, 165, 233, 0.35);
}

.button:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 30px rgba(14, 165, 233, 0.45);
}

.button:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.button--ghost {
  background: rgba(255, 255, 255, 0.08);
  color: #e2e8f0;
  box-shadow: none;
  border: 1px solid rgba(255, 255, 255, 0.12);
}

.button--record {
  background: linear-gradient(135deg, #f43f5e, #ef4444);
  color: #0b1224;
  box-shadow: 0 8px 20px rgba(244, 63, 94, 0.35);
}

.button--record-active {
  background: linear-gradient(135deg, #fb7185, #f43f5e);
}

.track-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.track {
  padding: 12px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.04);
}

.track__meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
  gap: 10px;
}

.track__name {
  color: #f1f5f9;
  font-weight: 600;
  word-break: break-word;
}

.track__controls {
  display: flex;
  align-items: stretch;
  gap: 12px;
  margin-bottom: 10px;
}

.icon-button {
  appearance: none;
  border: none;
  background: rgba(255, 255, 255, 0.06);
  color: #e2e8f0;
  width: 30px;
  height: 30px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  display: grid;
  place-items: center;
  transition: background 120ms ease, transform 120ms ease;
}

.icon-button:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateY(-1px);
}

.icon-button:active {
  transform: translateY(0);
}

.control-column {
  flex: 1 1 50%;
  display: flex;
  align-items: flex-start;
  gap: 8px;
  color: #cbd5e1;
  font-size: 14px;
}

.control-column.volume-column {
  flex: 0 1 50%;
}

.control-field {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.control-field__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.control-field__label {
  font-weight: 600;
  color: #e2e8f0;
}

.control-field__value {
  font-variant-numeric: tabular-nums;
  color: #e2e8f0;
}

.slider {
  appearance: none;
  height: 6px;
  width: 100%;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.15);
  outline: none;
}

.slider::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: linear-gradient(135deg, #38bdf8, #22d3ee);
  box-shadow: 0 6px 16px rgba(34, 211, 238, 0.35);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: linear-gradient(135deg, #38bdf8, #22d3ee);
  border: none;
  box-shadow: 0 6px 16px rgba(34, 211, 238, 0.35);
  cursor: pointer;
}

.track__progress {
  display: flex;
  align-items: center;
  gap: 12px;
}

.progress {
  position: relative;
  flex: 1;
  height: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 999px;
  overflow: hidden;
}

.progress__bar {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0%;
  background: linear-gradient(135deg, #38bdf8, #22d3ee);
  border-radius: 999px;
  transition: width 120ms linear;
}

.audio-hidden {
  display: none;
}
